.footer{
  padding: 2.6875em 0 12em;
  background-color: $darkBlue;
  color: #fff;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__col{
    padding-right: 1em;
    min-width: 12.25em;
    max-width: 16em;
    &:last-child{
      padding-right: 0;
    }
  }
  &__dropdown{
    margin-top: 1.25em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    color: #D9D9D9;
    &:not(:last-child){
      margin-bottom: .625em;
    }
    span{
      font-size: 1em;
      line-height: 140%;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2.9375em 0 2.3125em;
    &__inner{
      flex-wrap: wrap;
    }
    &__col{
      max-width: 33%;
      &:nth-child(4){
        width: 100%;
        margin-top: 4em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 1.5em 0 2.25em;
    &__inner{
      display: block;
    }
    &__col{
      max-width: unset;
      min-width: unset;
      padding: 0 0 .625em;
      &:nth-child(4){
        margin-top: 2em;
        padding: 0;
      }
    }
    &__toggle{
      display: flex;
      align-items: center;
      &:after{
        content: '';
        display: block;
        flex-shrink: 0;
        width: 1.5em;
        height: 1.5em;
        background-image: url(../img/arrow-down.svg);
        @include bgImgSize(100%);
        margin-left: .3125em;
        transition: all .3s;
      }
      &._active{
        &:after{
          transform: rotate(-180deg);
        }
      }
    }
    &__dropdown{
      display: none;
      padding-bottom: 1.25em;
    }
    .header-contacts__items{
      max-width: 13em;
    }
  }
}
