@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    border-radius: .5em;
    height: 2.625em;
    padding: 0 1.75em;
    span{
        font-size: 1em;
        line-height: 140%;
    }
}

.m-btn-blue{
    background-color: $blue;
    color: #fff;
    font-weight: 500;
    &:hover{
        background-color: $lightBlue;
        color: #fff;
    }
}

.m-btn-white{
    background-color: $white;
    color: $darkBlue;
    font-weight: 500;
    &:hover{
        background-color: $lightBlue;
        color: #fff;
    }
}

.text-link{
    font-size: 1em;
    line-height: 140%;
    text-decoration-line: underline;
    color: $darkBlue;
}

.favourite-btn{
    &__icon{
        transition: all .3s;
        display: block;
        width: 1.0625em;
        height: 1.0625em;
        background-image: url(../img/favourite.svg);
        @include bgImgSize(100%);
        &:hover{
            background-image: url(../img/favourite3.svg);
        }
    }
    &._added{
        .favourite-btn__icon{
            background-image: url(../img/favourite2.svg);
        }
    }
}

.swiper-button{
    width: 1.875em;
    height: 1.875em;
    transition: all .3s;
    opacity: 1 !important;
    &:hover{
        &:before{
            opacity: 1;
            background-color: $blue;
        }
    }
    &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $grey;
        border-radius: 50%;
        transition: inherit;
        opacity: .5;
    }
    svg{
        position: relative;
        z-index: 2;
        width: .5625em;
        height: .875em;
        fill: none;
        stroke: #fff;
    }
}

.play-btn{
    width: 4.375em;
    display: block;
    @include centerAbsolute;
    img{
        width: 100%;
        display: block;
    }
}


@media screen and (max-width: 992px){
    .play-btn{
        width: 5.375em;
    }
}


@media screen and (max-width: 576px){
    .text-link{
        font-size: .875em;
    }

    .play-btn{
        width: 4.625em;
    }
}
