.text32,
.text28,
.text18{
  font-weight: 600;
  line-height: 140%;
}

.text42{
  font-weight: 600;
  font-size: 2.625em;
  line-height: 122%;
}

.text32{
  font-size: 2em;
}

.text28{
  font-size: 1.75em;
}

.text20{
  font-size: 1.25em;
  line-height: 140%;
}

.text18{
  font-size: 1.125em;
}

.text16{
  font-size: 1em;
  line-height: 140%;
}

.text15{
  font-size: .9375em;
  line-height: 140%;
}

.text14{
  font-size: .875em;
  line-height: 140%;
}

.text12{
  font-size: .75em;
  line-height: 140%;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.light-text{
  font-weight: 300;
}

.font2{
  font-family: $font2;
}

.dark-blue-text{
  color: $darkBlue;
}

.blue-text{
  color: $blue;
}

.grey-text{
  color: $grey;
}

.green-text{
  color: $green;
}


@media screen and (max-width: 576px){
  .text42{
    font-size: 2em;
  }

  .text32{
    font-size: 1.25em;
  }

  .text20{
    font-size: .875em;
  }

  .text15{
    font-size: .75em;
  }

  .text12{
    font-size: .625em;
  }
}
