//index page styles
.main{
  padding: 2.25em 0;
}

.main-slider{
  overflow: hidden;
  position: relative;
  border-radius: .5em;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-pagination{
      bottom: .875em;
      display: flex;
      justify-content: center;
      &-bullet{
        background-color: #fff;
        opacity: 0.2;
        width: .625em;
        height: .625em;
        margin: 0 .625em !important;
        &:hover,
        &-active{
          opacity: 1;
        }
      }
    }
    &-button{
      &-prev{
        left: 1.5em;
      }
      &-next{
        right: 1.5em;
      }
    }
  }
}

.main-slide{
  height: auto;
  padding: 6.125em 6.25em 5.5em;
  background-size: cover;
  background-position: right center;
  background-color: $black;
  border-radius: .5em;
  position: relative;
  color: #fff;
  &__content{
    max-width: 41.5em;
    @include grid100gap(1.25em);
  }
  &__nav{
    margin-top: 1.25em;
    display: flex;
  }
  &__btn{
    font-weight: 600;
    &:not(:last-child){
      margin-right: 1.25em;
    }
  }
}

.block-top{
  display: flex;
  align-items: flex-end;
  &__nav{
    padding: 0 0 .375em 1.875em;
  }
}

.catalog{
  padding: .5em 0 2.375em;
  &__grid{
    margin-top: 1.5625em;
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 6.25em / 6));
    gap: 1.25em;
  }
  @media screen and (min-width: 577px){
    .catalog-group{
      display: block;
      background: #FFFFFF;
      border: 1px solid $tabGrey;
      border-radius: .5em;
      padding: 1.5em 1.25em;
      min-height: 16.5625em;
      transition: all .3s;
      &:hover{
        box-shadow: $shadow;
      }
      &__links{
        display: none;
      }
      &__img{
        height: 10.25em;
        @include center;
        margin-bottom: .625em;
        img{
          width: auto;
          max-width: 100%;
          max-height: 100%;
        }
      }
    }
  }
}

.offer{
  padding: 2.5em 0 1.75em;
  background-color: $lightGrey;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__grid{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 6em / 5));
    gap: 1.5em;
  }
}

.services{
  padding: 2.5em 0;
  &__grid{
    margin-top: 2em;
  }
}

.promo{
  padding: 2.5em 0 1.75em;
  background-color: $lightGrey;
  &__grid{
    margin-top: 2.5em;
  }
}

.stock{
  padding: 2.5em 0;
  color: #FFFFFF;
  &__inner{
    background-size: cover;
    background-position: right center;
    border-radius: .5em;
    padding: 4em 7.5em 4.25em;
  }
  &__info{
    @include grid100gap(1.25em);
  }
  &__nav{
    margin-top: 2.375em;
    display: flex;
  }
  &__btn{
    padding: 0 1.5em;
    height: 2.875em;
    color: $black;
  }
}

.about{
  padding: 4.375em 0 3.75em;
  background-color: $lightGrey;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 37.5em - 2.75em) 37.5em;
    gap: 2.75em;
  }
  &__img{
    img{
      display: block;
      width: 100%;
      border-radius: .5em;
    }
  }
  &__text{
    margin-top: 1.5em;
    @include grid100gap(1em);
    p{
      line-height: inherit;
    }
  }
  &__nav{
    margin-top: 2em;
    display: flex;
  }
  &__btn{
    padding: 0 2.1875em;
    height: 3.25em;
  }
}

.advantages{
  padding: 3.75em 0 3em;
}

.reviews{
  padding: 2.5em 0 2.125em;
  background-color: $lightGrey;
  &__grid{
    margin-top: 2.5em;
  }
  .review-card{
    &:nth-child(1){
      font-weight: 600;
    }
  }
}

.brands{
  padding: 2.5em 0 7.1875em;
  &__slider{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: 1.875em calc(100% - 10em) 1.875em;
    gap: 3.125em;
  }
  .swiper{
    &-button{
      position: relative;
      margin: 0;
    }
    &-container{
      width: calc(100% + 2.5em);
      overflow: hidden;
    }
    &-slide{
      padding-right: 2.5em;
    }
    @media screen and (min-width: 577px){
      &-scrollbar{
        display: none;
      }
      &-slide {
        width: calc(100% / 6);
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 1.75em 0;
  }

  .main-slider{
    .swiper{
      &-pagination{
        bottom: 1.625em;
      }
      &-button{
        display: none;
      }
    }
  }

  .main-slide{
    padding: 2.375em 2.5em 4em;
    min-height: 37.5em;
    &__content{
      max-width: 31.25em;
    }
  }

  .catalog{
    padding: .25em 0 3.75em;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 4em / 3));
      gap: 2em;
    }
    @media screen and (min-width: 577px){
      .catalog-group{
        min-height: 20.3125em;
        &__img{
          height: 12.5em;
        }
        &__title{
          font-size: 1.25em;
        }
      }
    }
  }

  .offer{
    padding: 1.875em 0 3.75em;
    &__top{
      display: block;
    }
    .tabs{
      margin-top: 1em;
    }
    &__grid{
      margin-top: 2.5em;
      grid-template-columns: repeat(3,calc(100% / 3 - 4em / 3));
      gap: 2em;
    }
  }

  .services{
    padding: 2em 0 3.25em;
    &__grid{
      margin-top: 1.5em;
    }
  }

  .promo{
    padding: 2em 0 3.5em;
    background-color: $lightGrey;
    &__grid{
      margin-top: 2em;
    }
  }

  .stock{
    padding: 0 0 3.5em;
    &__inner{
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      padding: 3.25em 2.5em;
      min-height: 25.625em;
      background-size: auto 100%;
    }
    &__info{
      gap: .25em;
      .text14{
        font-size: 1em;
      }
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .about{
    padding: 2.5em 0 3.25em;
    &__grid{
      grid-template-columns: 100%;
      gap: 2.1875em;
      margin-top: 2.1875em;
    }
    &__img{
      order: -1;
    }
  }

  .advantages{
    padding: 3em 0 3.75em;
    &__grid{
      margin-top: 2.8125em;
      row-gap: 4.4375em;
    }
  }

  .reviews{
    padding-bottom: 2.75em;
    &__grid{
      margin-top: 2.1875em;
    }
  }

  .brands{
    padding-top: 1.25em;
    &__slider{
      grid-template-columns: 1.875em calc(100% - 8.25em) 1.875em;
      gap: 2.25em;
    }
    .swiper{
      @media screen and (min-width: 577px){
        &-slide{
          width: calc(100% / 3);
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding: .5em 0;
    .container{
      padding: 0;
    }
  }

  .main-slider{
    .swiper{
      &-pagination{
        bottom: .6875em;
      }
    }
  }

  .main-slide{
    min-height: 25em;
    padding: 2.125em .625em;
    background-position: right 14% center;
    .text42{
      br{
        display: none;
      }
    }
    .text16{
      max-width: 13.75em;
    }
    &__nav{
      display: none;
    }
  }

  .block-top{
    &__nav{
      padding: 0 0 .125em .75em;
    }
  }

  .catalog{
    padding: 1em 0 2.5em;
    &__grid{
      grid-template-columns: 100%;
      gap: 2.375em;
      margin-top: 1.5em;
    }
  }

  .offer{
    padding: 1.25em 0 2.875em;
    .tabs{
      margin-top: .6875em;
    }
    &__grid{
      margin-top: 1.375em;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
  }

  .services{
    padding: 1.25em 0 2.5em;
    &__grid{
      margin-top: 1.375em;
    }
  }

  .promo{
    padding: 1em 0 1.875em;
    &__grid{
      margin-top: 1.25em;
    }
  }

  .stock{
    padding: 2.1875em 0 2.875em;
    .container{
      padding: 0;
    }
    &__inner{
      display: block;
      padding: 2.1875em .625em .875em;
      min-height: unset;
    }
    &__info{
      .text32{
        font-size: 2em;
      }
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .about{
    padding: 1.375em 0 2.5em;
    &__grid{
      gap: 1.25em;
      margin-top: 1.25em;
    }
    &__text{
      margin-top: 1.25em;
    }
  }

  .advantages{
    padding: 2.25em 0 3.125em;
    &__grid{
      margin-top: 1.875em;
      gap: 3.3125em;
    }
  }

  .reviews{
    padding: 1.5em 0 2em;
    &__grid{
      margin-top: 2em;
    }
  }

  .brands{
    padding: 2.375em 0 0;
    &__slider{
      grid-template-columns: 100%;
      gap: 0;
      padding-bottom: 1.875em;
      margin-top: 1.375em;
      position: relative;
    }
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        padding-right: 1em;
      }
      &-button{
        display: none;
      }
      &-scrollbar{
        position: absolute;
        bottom: 0;
        left: -.625em;
        width: calc(100% + 1.25em);
        height: .625em;
        background: #F9F9FB;
        border: 1px solid $tabGrey;
        padding: .125em .375em;
        &-drag{
          background: #AEAEAE;
          opacity: 0.4;
          border-radius: .5em;
        }
      }
    }
  }
}
//end index page styles
