//cards styles
.catalog-card{
  padding: 1.25em;
  background: #FFFFFF;
  border: 1px solid $tabGrey;
  border-radius: .5em;
  position: relative;
  display: flex;
  flex-direction: column;
  transition: all .3s;
  &:hover{
    box-shadow: $shadow;
  }
  &__badges{
    position: absolute;
    top: 1.25em;
    left: 0;
    width: 100%;
    padding: 0 .9375em 0 1.25em;
  }
  &__img{
    padding-bottom: 111.67%;
    position: relative;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__info{
    margin-top: .625em;
    @include grid100gap(.3125em);
  }
  &__bottom{
    margin-top: auto;
    padding-top: .625em;
    @include grid100gap(.625em);
  }
  &__info2{
    display: flex;
    align-items: center;
  }
  &__rating{
    display: flex;
    align-items: center;
    img{
      width: 1em;
      display: block;
      flex-shrink: 0;
      margin-right: .125em;
    }
    span{
      font-size: .75em;
    }
  }
  &__has{
    margin-left: .875em;
  }
  &__nav{
    display: grid;
    grid-template-columns: auto 1.0625em;
    gap: .8125em;
  }
}

.service-card,
.promo-card{
  &__img{
    img{
      border-radius: .5em;
    }
  }
  &__info{
    margin-top: 1.25em;
    @include grid100gap(.9375em);
  }
  &__title{
    @include truncate(2);
  }
}

.service-card{
  display: flex;
  flex-direction: column;
  &__img{
    @include imgCover(51.3%);
    img{
      border-radius: .5em;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: .625em;
    display: flex;
  }
  &__btn{
    padding: 0 1.25em;
    height: 2.375em;
    span{
      font-size: .875em;
    }
  }
  &__desc{
    @include truncate(4);
  }
}

.promo-card{
  &__img{
    @include imgCover(63.15%);
  }
  &__desc{
    color: #838383;
  }
}

.advantage-card{
  display: grid;
  grid-template-columns: 3.75em auto;
  gap: 1.5em;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    margin-top: .9375em;
  }
}

.review-card{
  display: block;
  &__top{
    position: relative;
  }
  &__img{
    margin-bottom: 1.25em;
    @include imgCover(61.6%);
    img{
      border-radius: .5em;
    }
  }
  &__title{
    @include truncate(2);
  }
}

.brand-card{
  &__img{
    position: relative;
    padding-bottom: 50%;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .review-card{
    &__img{
      margin-bottom: 1.5em;
    }
  }
}



@media screen and (max-width: 576px){
  .catalog-card{
    padding: .625em;
    &__badges{
      top: .625em;
      padding: 0 .375em 0 .625em;
      align-items: flex-start;
      ._big-badge,
      ._small-badge{
        word-break: break-all;
        padding: .1875em .375em;
        justify-content: center;
        text-align: center;
        height: auto;
      }
      ._small-badge{
        width: 1.875em;
      }
      ._big-badge{
        width: 3.75em;
      }
    }
    &__info{
      margin-top: .3125em;
      gap: 0;
    }
    &__bottom{
      padding-top: .3125em;
      gap: .5em;
    }
    &__rating{
      span{
        font-size: .75em;
      }
    }
    &__nav{
      grid-template-columns: auto .75em;
      gap: .5em;
    }
    &__btn{
      height: 1.75em;
      padding: 0;
      border-radius: .3125em;
      span{
        font-size: .625em;
      }
    }
    .favourite-btn{
      &__icon{
        width: .75em;
      }
    }
  }

  .service-card{
    &__img{
      padding-bottom: 64.7%;
    }
  }

  .promo-card{
    &__img{
      padding-bottom: 79.73%;
    }
  }
}
//end cards styles
