//ui elements
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.tabs{
  display: flex;
  align-items: center;
}

.tab{
  font-family: $font2;
  display: flex;
  align-items: center;
  padding: 0 .625em;
  height: 1.875em;
  border-radius: .5em;
  background-color: $tabGrey;
  color: $black;
  font-weight: 500;
  transition: all .3s;
  cursor: pointer;
  &:not(:last-child){
    margin-right: 1.25em;
  }
  span{
    font-size: .875em;
  }
  &:hover,
  &._active{
    background-color: $blue;
    color: #FFFFFF;
  }
  &._active{
    pointer-events: none;
  }
  &-content{
    display: none;
    &._active{
      display: block;
    }
  }
}

.badges{
  display: flex;
  flex-wrap: wrap;
  margin-top: -.3125em;
}

.badge{
  display: flex;
  align-items: center;
  margin: .3125em .3125em 0 0;
  padding: 0 .625em;
  height: 1.875em;
  background-color: $red;
  border-radius: .5em;
  color: #FFFFFF;
  font-weight: 500;
  span{
    font-size: .875em;
    line-height: 140%;
  }
  &._yellow-bg{
    color: $black;
  }
}

._purple-bg{
  background-color: $lavender;
}

._green-bg{
  background-color: $green;
}

._yellow-bg{
  background-color: $promo;
}

.grid-column4{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - .9375em));
  gap: 1.25em;
}

.grid-column3{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
  gap: 1.875em;
}


@media screen and (min-width: 993px){
  ._desktop-hide{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .grid-column4{
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }

  .grid-column3{
    grid-template-columns: repeat(2,calc(50% - .875em));
    gap: 3.25em 1.75em;
  }
}


@media screen and (max-width: 576px){
  .tab{
    height: 1.6875em;
    &:not(:last-child){
      margin-right: .5em;
    }
    span{
      font-size: .75em;
    }
  }

  .grid-column4{
    grid-template-columns: 100%;
    gap: 2.375em;
  }

  .grid-column3{
    grid-template-columns: 100%;
    gap: 2em;
  }
}
//end ui elements
